import React from 'react';
import EntityContainer from 'modules/entity/containers';
import { Pagination, TableBody, TableContainer, TableHead } from 'shared/components/Elements';
import {
	ActionsButtons,
	ConfirmModal,
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import { Link } from 'react-router-dom';
import dayjs from 'dayjs';
import { useAppState } from 'shared/state';
import { useDebounce } from 'use-debounce';
import { usePagination, useRouter } from 'shared/hooks';
import { useNewsContext } from '../../context/NewsContext';
import { useNewsList } from '../../hooks';
import { NewsEntity } from 'shared/typings/news.types';

export function NewsList() {
	const { companyId, isPermitted } = useAppState();
	const { page, handlePage } = usePagination();
	const { searchQuery: query, isStaff } = useNewsContext();
	const { history } = useRouter();

	const [searchQuery] = useDebounce<string>(query, 500);

	const { deleteLoading, handleNewsDelete, showDeleteVisible, closeDeletePopup, deleteVisible } =
		useNewsList();

	const deleteNewsHandler = () => () => handleNewsDelete(deleteVisible!);

	return (
		<div>
			{companyId ? (
				<EntityContainer.All
					entity="news"
					name="AllNews"
					url={`/${companyId}/notifications/`}
					params={{
						limit: 10,
						extra: {
							search: searchQuery,
							type: isStaff ? 'staff' : 'client',
							as_staff: 'true'
						},
						page
					}}
				>
					{({ items, isFetched, meta }) => {
						const newsItems = items as NewsEntity[];
						return (
							<>
								<div>
									<TableContainer classes={{ root: 'relative ' }}>
										<StyledTable
											aria-label="simple table"
											classes={{ root: 'table-fixed dark-mode-styled-table' }}
										>
											<TableHead>
												<StyledTableRow>
													<StyledTableHeadCell style={{ width: '20%' }}>Title</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '10%' }}>Status</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '10%' }}>
														Frequency
													</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '20%' }}>Author</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '20%' }}>Date</StyledTableHeadCell>
													{isPermitted && (
														<StyledTableHeadCell style={{ width: '20%' }}>
															<div className=" flex-center-y">
																<div className="ml-2">Actions</div>
															</div>
														</StyledTableHeadCell>
													)}
												</StyledTableRow>
											</TableHead>
											<TableBody>
												{newsItems.map((item) => {
													const {
														frequency,
														start_time,
														status,
														author,
														subject,
														id,
														have_all_permissions
													} = item ?? {};
													return (
														<StyledTableRow key={id}>
															<StyledTableCell>
																<Link to={`/news/view/${id}`}>
																	<span className="text-primary transition hover:text-primary-dark">
																		{subject}
																	</span>
																</Link>
															</StyledTableCell>
															<StyledTableCell>{status}</StyledTableCell>
															<StyledTableCell>{frequency}</StyledTableCell>
															<StyledTableCell>
																<div className={'truncate'}>{author.name}</div>
															</StyledTableCell>
															<StyledTableCell>
																{dayjs(start_time).format('MMMM D YYYY, h:mm a')}
															</StyledTableCell>
															{have_all_permissions && (
																<StyledTableCell>
																	<div className="flex-center-x">
																		<ActionsButtons.DeleteButton
																			clickHandler={() => showDeleteVisible(item)}
																		/>
																		<ActionsButtons.ViewButton
																			clickHandler={() => history.push(`/news/view/${item.id}`)}
																		/>
																		<ActionsButtons.EditButton
																			clickHandler={() => history.push(`/news/edit/${item.id}`)}
																		/>
																	</div>
																</StyledTableCell>
															)}
														</StyledTableRow>
													);
												})}
											</TableBody>
										</StyledTable>
										{!isFetched && <Loader tiled withOverlay />}
									</TableContainer>
									<ConfirmModal
										featureHandler={deleteNewsHandler()}
										isOpen={Boolean(deleteVisible)}
										toggleHandler={closeDeletePopup}
										title={`Are you sure to delete  ${deleteVisible?.subject}?`}
										hasLoader
										isLoading={deleteLoading}
									/>
									<div className="news__pagination my-4">
										{items.length ? (
											<Pagination
												count={meta?.last_page}
												page={page ? +page : 1}
												defaultPage={6}
												siblingCount={1}
												color={'primary'}
												onChange={handlePage}
											/>
										) : null}
									</div>
								</div>
							</>
						);
					}}
				</EntityContainer.All>
			) : (
				<div className="flex justify-center">
					<Loader />
				</div>
			)}
		</div>
	);
}
